import { FineCanvas } from './canvas';
import { dpr } from '../../utils/dom';
import { Element, h } from '../../component/element';
import { CanvasOptions, Bounds } from 'types';

export function canvas(rootEl: Element, options: CanvasOptions): FineCanvas {
    const canvas = h('canvas', 'sheet-table-canvas');
    const offset = rootEl.offset() as Bounds;
    const { width = offset.width, height = offset.height } = options;
    const canvasEl = canvas.el as HTMLCanvasElement;
    rootEl.children(canvasEl);
    const pixelRatio = dpr();
    canvasEl.width = width * pixelRatio;
    canvasEl.height = height * pixelRatio;
    canvas.offset({
        width,
        height,
    });

    return new FineCanvas(canvasEl, options);
}
